import React, { FC, memo, Suspense, useEffect, useRef, useState } from "react";
import { useHistory } from "react-router-dom";
import debounce from "lodash.debounce";
import clsx from "clsx";

import { useWhiteTheme } from "providers";
import { createSuspenseComponent, isElementOnDisplay } from "utils";
import {
  Button,
  ButtonProps,
  Dots,
  OptimizedSlider,
  SliderValues,
  useAutoScroll,
} from "components";

import { ReactComponent as ArrowRight } from "assets/icons/chevron-right.svg";
import { ReactComponent as ArrowLeft } from "assets/icons/chevron-left.svg";

import Loader from "config/LazyComponentLoader";
import css from "./style.module.scss";

import Bonus from "./Bonus";
import { useVisibility } from "hooks";

const BonusBanner = memo(Bonus);
const HashDiceBanner = memo(createSuspenseComponent(import("./HashDice")));
const CrashBanner = memo(createSuspenseComponent(import("./Crash")));
const SlotsBanner = memo(createSuspenseComponent(import("./Slots")));
const WageringRaceBanner = memo(
  createSuspenseComponent(import("./WageringRace")),
);

// const LotteryBanner = createSuspenseComponent(import("./Lottery"));

const EventDecoration = React.lazy(() => Loader(import("./EventDecoration")));

const banners: FC<{
  play: boolean;
}>[] = [
  BonusBanner,
  HashDiceBanner,
  CrashBanner,
  SlotsBanner,
  WageringRaceBanner,
  // LotteryBanner, // not done
];

const links = [
  "/bonus",
  "/hash-dice",
  "/crash",
  "/slot-games",
  "/hash-dice?link=wagering",
];

interface Props extends ButtonProps {
  direction: "left" | "right";
}

const Arrow: FC<Props> = ({ direction, className, ...props }) => {
  return (
    <Button
      className={clsx(
        css.button,
        direction === "right" && css.button__right,
        className,
      )}
      {...props}>
      {direction === "right" ? <ArrowRight /> : <ArrowLeft />}
    </Button>
  );
};

const Banners = () => {
  const history = useHistory();
  const { activeEvent } = useWhiteTheme();

  const { isHidden } = useVisibility();
  const sliderRef = useRef<SliderValues | null>(null);
  const bannersWrapperRef = useRef<HTMLDivElement | null>(null);
  const [activeIndex, setActiveIndex] = useState(0);

  const increment = () => {
    sliderRef.current?.increment();
  };
  const decrement = () => {
    sliderRef.current?.decrement();
  };

  const { start, stop, status } = useAutoScroll(increment, 5000);

  const handleOnClick = (index: number) => {
    history.push(links[index]);
  };

  useEffect(() => {
    const isOnDisplay = debounce(() => {
      if (bannersWrapperRef.current)
        isElementOnDisplay(bannersWrapperRef.current, start, () => {
          stop();
          setActiveIndex(banners.length - 1);
        });
    }, 300);

    window.addEventListener("scroll", isOnDisplay);

    return () => {
      window.removeEventListener("scroll", isOnDisplay);
    };
  }, [bannersWrapperRef.current]);

  useEffect(() => {
    if (isHidden) stop();
    else if (!status.isStarted) start();
  }, [isHidden]);

  return (
    <div className={css.wrapper} ref={bannersWrapperRef}>
      {activeEvent && (
        <Suspense fallback={null}>
          <EventDecoration eventName={activeEvent} />
        </Suspense>
      )}

      <Arrow direction="left" onClick={decrement} />
      <Arrow direction="right" onClick={increment} />

      <OptimizedSlider
        dataLength={banners.length}
        cardsAmount={1}
        ref={sliderRef}
        currentIndex={activeIndex}
        setActualIndex={setActiveIndex}
        onMouseOver={stop}
        onMouseLeave={start}>
        {banners.map((Component, i) => {
          return (
            <div key={`banner_unit_${i}_`} onClick={() => handleOnClick(i)}>
              <Suspense fallback={null}>
                <Component play={!isHidden && activeIndex === i} />
              </Suspense>
            </div>
          );
        })}
      </OptimizedSlider>
      <div className={css.dotsContainer}>
        <Dots
          actual={activeIndex + 1}
          amount={banners.length}
          onClick={setActiveIndex}
          secondary
        />
      </div>
    </div>
  );
};

export default Banners;
