import React from "react";
import clsx from "clsx";

import { Button } from "components";

import bonusUrl from "./assets/bonus.svg";
import buttonText from "./assets/buttonText.svg";
import light from "./assets/light.svg";
import rightWing from "./assets/rightWing.svg";
import leftWing from "./assets/leftWing.svg";

import css from "./style.module.scss";

export const Title = () => {
  return (
    <>
      <img className={css.mainTitle} src={bonusUrl} alt="bonuses" />
      <h2 className={css.secondTitle}>
        Unlock huge bonuses and get up to 0.75% Rakeback
      </h2>

      <span className={css.buttonWrapper}>
        <img src={light} alt="light" className={css.light} />
        <Button className={css.button}>
          <img src={buttonText} alt="play now" className={css.buttonText} />
        </Button>
        <img
          src={rightWing}
          alt="rightWing"
          className={clsx(css.rightWing, css.wing)}
        />
        <img
          src={leftWing}
          alt="leftWing"
          className={clsx(css.leftWing, css.wing)}
        />
      </span>
    </>
  );
};
