import React from "react";

import little_01 from "./imgs/little_01.svg";
import little_02 from "./imgs/little_02.svg";
import medium_01 from "./imgs/medium_01.svg";

import css from "./style.module.scss";
import { BigReward } from "./components";

export const Rewards = () => {
  return (
    <>
      <img
        src={little_01}
        alt="little reward"
        className={css.littleReward_01}
      />

      <img
        src={little_02}
        alt="little reward"
        className={css.littleReward_02}
      />

      <img
        src={medium_01}
        alt="medium reward"
        className={css.mediumReward_01}
      />

      <BigReward />
    </>
  );
};
