import clsx from "clsx";
import React from "react";

import _1 from "./assets/balls/_1.svg";
import _2 from "./assets/balls/_2.svg";
import _3 from "./assets/balls/_3.svg";
import _4 from "./assets/balls/_4.svg";
import _5 from "./assets/balls/_5.svg";
import _6 from "./assets/balls/_6.svg";
import _7 from "./assets/balls/_7.svg";
import css from "./style.module.scss";

const imgs = [_1, _2, _3, _4, _5, _6, _7];

export const Bulls = () => {
  return (
    <>
      {imgs.map((src, i) => (
        <img
          src={src}
          alt={`ball ${i}`}
          className={clsx(css.ball, css[`ball__${i + 1}`])}
        />
      ))}
    </>
  );
};
