import React, { FC } from "react";

import { Rewards, Title } from "./components";
import background from "./background.webp";
import css from "./style.module.scss";
import clsx from "clsx";

interface Props {
  play: boolean;
}

const BonusBanner: FC<Props> = ({ play }) => {
  return (
    <div className={clsx(css.container, !play && css.disable)}>
      <img src={background} alt="bg" className={css.bg} />
      <Title />
      <Rewards />
    </div>
  );
};

export default BonusBanner;
