import React from "react";

import root from "./assets/root.svg";
import cup from "./assets/cup.svg";
import lights from "./assets/lights.svg";

import { Bulls } from "./Bulls";
import css from "./style.module.scss";

export const BigReward = () => {
  return (
    <div className={css.wrapper}>
      <img src={root} alt="root img" className={css.root} />
      <img src={lights} alt="lights img" className={css.lights} />
      <img src={cup} alt="cup img" className={css.cup} />

      <Bulls />
    </div>
  );
};
